<template>
    <p-fieldset legend="Pregnancy" class="p-inputtext-sm" id="pregnancy">
        <div class="p-grid">
            <div class="p-col-12 p-md-6">
                <pregnancy-tests />
            </div>
            <div class="p-col-12 p-md-6">
                <pregnancy-fetus-histories />
            </div>
            <div class="p-col-12 p-md-6">
                <pregnancy-outcome-form />
            </div>
            <div class="p-col-12 p-md-6">
                <pregnancy-live-birth-hisory />
                <div class="p-fluid p-formgrid p-grid p-mt-3">
                    <div class="p-field p-col-12">
                        <label for="">Comments</label>
                        <p-textarea />
                    </div>
                </div>
            </div>
        </div>

        <p-button label="save" class="p-button-success p-mt-2" />
    </p-fieldset>
</template>

<script>
import { onMounted, ref } from "vue";
import { usePregnancyInit } from "@/composable/pregnancy";

import PregnancyTests from "@/components/pregnancy/tests.vue";
import PregnancyFetusHistories from "@/components/pregnancy/fetus-history.vue";
import PregnancyOutcomeForm from "@/components/pregnancy/outcome-form.vue";
import PregnancyLiveBirthHisory from "@/components/pregnancy/live-birth-hisory.vue";

export default {
    components: {
        PregnancyTests,
        PregnancyFetusHistories,
        PregnancyOutcomeForm,
        PregnancyLiveBirthHisory
    },
    setup() {
        const date = ref();
        const testResult = ref([]);

        const { store, toast, getPregnancyDetails } = usePregnancyInit();

        onMounted(() => {
            if (store.state.artCycle.selectedCycle) {
                getPregnancyDetails(store.state.artCycle.selectedCycle.cycleId);
            } else {
                toast.add({
                    severity: "warn",
                    summary: "",
                    detail: "Please Select Patient First",
                    life: 3000
                });
            }
            // getPregnancyDetails(150002420);
        });

        return {
            date,
            testResult
        };
    }
};
</script>

